import React, { Component } from 'react';
import HelloWorld from './HelloWorld';
import AddGreeter from './AddGreeter';

class HelloWorldList extends Component {

    constructor(props) {
        super(props);
        this.state = { names: ['Chris', 'Jimmy', 'Sally']};
        this.addGreeting = this.addGreeting.bind(this);
        this.removeGreeting = this.removeGreeting.bind(this);
    }

    renderGreetings() {
        return this.state.names.map(name => (
            <HelloWorld key={name} name={name} removeGreeting={this.removeGreeting}/>
        ));
    }

    addGreeting(newName) {
        this.setState({ names: [...this.state.names, newName] });
    }

    removeGreeting(removeName) {
        const filteredGreetings = this.state.names.filter(name => {
            return name !== removeName;
        });
        this.setState({ names: filteredGreetings });
    }

    render() {
        return (
            <div className="HelloWorldList">
                <AddGreeter addGreeting={this.addGreeting}/>
                {this.renderGreetings()}
            </div>
        )
    }
}

export default HelloWorldList;


